* {
  box-sizing: border-box;
}
h3 {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bolder;
  color: #17a589;
  padding: 50px;
  line-height: 30px;
}
.cars {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
  color: #1b165a;
  padding: 20px;
  font-family: Arial;
  background: url(../../assets/fon3.jpg) no-repeat;
}
.cars p {
  justify-content: center;
  font-size: 20px;
  padding-bottom: 10px;
  color: #0e025e;
}
.cars .row {
  justify-content: center;
  width: 100%;
}
.contact .row:after {
  content: "";
  display: table;
  clear: both;
}
.card {
  width: 200px;
  margin: 10px;
  padding: 10px;
  border: 1px solid #1c0a93;
  border-radius: 3px;
  transition: box-shadow 0.3s ease-in-out;
}
.card h2 {
  font-weight: normal;
  color: #146928;
  text-shadow: 3px 3px 1px #c7f1d1;
}
.card:hover {
  box-shadow: 2px 2px 0 #1c0a93;
}

.card.marked {
  border: 2px solid #1c0a93;
}

.card-img {
  display: flex;
  align-items: center;
  height: 150px;
}

.card img {
  width: 100%;
}
@media screen and (max-width: 900px) {
  .column {
    width: 50%;
  }
}
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}
